import { RegisterComponent } from '@/common/register-component';
import { debug } from '@/utils/utils';
import { Component, Element, Event, EventEmitter, Host, Listen, Method, Prop, h } from '@stencil/core';
import { NylasMailboxConnector } from '../../..';
import { NylasMailbox } from '../nylas-mailbox/nylas-mailbox';

export type PaginationEvent = {
  hasChildren: boolean;
};

@Component({
  tag: 'nylas-mailbox-pagination',
  styleUrl: 'nylas-mailbox-pagination.scss',
  shadow: true,
})
export class NylasMailboxPagination {
  /**
   * @description The host element
   */
  @Element() host!: HTMLNylasMailboxPaginationElement;

  /**
   * @description The state of the mailbox
   */
  @Prop() state: 'ready' | 'loading' | 'error' = 'ready';

  /**
   * @description Whether or not the mailbox has a next or previous page of threads
   */
  @Prop() hasNextPage: boolean = false;

  /**
   * @description Whether or not the mailbox has a previous page of threads
   */
  @Prop() hasPreviousPage: boolean = false;

  /**
   * @description Whether or not the mailbox has a next or previous page of threads
   */
  @Prop() scrollTarget: 'container' | 'page' = 'container';

  /**
   * @description Whether or not the infinity scroll should only be enabled on mobile devices
   */
  @Prop() mobileOnlyInfiniteScroll: boolean = true;

  /**
   * @description The width of the mobile viewport
   */
  @Prop() mobileViewPortWidth: number = 768;

  /**
   * @description An event that is fired when the user scrolls to the bottom of the page
   */
  @Event() next!: EventEmitter<PaginationEvent>;

  /**
   * @description An event that is fired when the user scrolls to the top of the page
   */
  @Event() previous!: EventEmitter<PaginationEvent>;

  /**
   * @description The container element
   */
  private containerElement?: HTMLDivElement;

  constructor() {
    debug(`[nylas-pagination] Constructed`);
    this.isScrollAtContainerBottom = this.isScrollAtContainerBottom.bind(this);
    this.isScrollAtPageBottom = this.isScrollAtPageBottom.bind(this);
    this.nextPage = this.nextPage.bind(this);
    this.previousPage = this.previousPage.bind(this);
    this.handlePageScroll = this.handlePageScroll.bind(this);
    this.handleContainerScroll = this.handleContainerScroll.bind(this);
  }

  connectedCallback() {
    debug(`[nylas-pagination] Component connected`);
  }

  async componentWillLoad() {
    debug(`[nylas-pagination] Component will load`);
  }

  async componentDidLoad() {
    debug(`[nylas-pagination] Component did load`);
    if (this.containerElement) {
      this.containerElement.addEventListener('scroll', this.handleContainerScroll);
    }
  }

  async componentWillRender() {
    debug(`[nylas-pagination] Component will render`);
  }

  disconnectedCallback() {
    debug(`[nylas-pagination] Component disconnected`);
    if (this.containerElement) {
      this.containerElement.removeEventListener('scroll', this.handleContainerScroll);
    }
  }

  /**
   * @description Listen for scroll events and fire the next page event when the user scrolls to the bottom of the page
   * @param _event An event that is fired when the user scrolls
   */
  @Listen('scroll', { target: 'window' })
  public handlePageScroll(_event: Event) {
    if (this.isScrollAtPageBottom() && this.canScroll && this.scrollTarget === 'page') {
      this.nextPage();
    }
  }

  /**
   * @description Go to the next page of threads
   */
  @Method()
  public async nextPage() {
    const hasChildren = this.host.children.length > 0;
    this.next.emit({
      hasChildren,
    });
  }

  /**
   * @description Go to the previous page of threads
   */
  @Method()
  public async previousPage() {
    const hasChildren = this.host.children.length > 0;
    this.previous.emit({
      hasChildren,
    });
  }

  /**
   * @description Handle the scroll event
   * @param event An event that is fired when the user scrolls
   */
  private handleContainerScroll(_event: Event) {
    if (this.isScrollAtContainerBottom() && this.canScroll && this.scrollTarget === 'container') {
      this.nextPage();
    }
  }

  /**
   * @description Check if the user has scrolled to the bottom of the page
   */
  private isScrollAtContainerBottom() {
    if (!this.containerElement) {
      return false;
    }
    return this.containerElement && this.containerElement.scrollTop + this.containerElement.clientHeight >= this.containerElement.scrollHeight;
  }

  /**
   * @description Check if the user has scrolled to the bottom of the page
   */
  private isScrollAtPageBottom() {
    return window.innerHeight + window.scrollY >= document.body.offsetHeight;
  }

  /**
   * @description Check if the user can scroll to the next page
   * @returns boolean
   */
  private get canScroll() {
    const hasChildren = this.host.children.length > 0;
    return this.state === 'ready' && hasChildren && (!this.mobileOnlyInfiniteScroll || window.innerWidth < this.mobileViewPortWidth);
  }

  @RegisterComponent<NylasMailboxPagination, NylasMailboxConnector, Exclude<NylasMailbox['stores'], undefined>>({
    name: 'nylas-pagination',
    stateToProps: new Map([
      ['mailbox.hasNextThreadPage', 'hasNextPage'],
      ['mailbox.hasPreviousThreadPage', 'hasPreviousPage'],
      ['mailbox.state', 'state'],
    ]),
    eventToProps: {
      next: async (event: CustomEvent<PaginationEvent>, nylasConnector: NylasMailboxConnector) => {
        debug(`[nylas-pagination] Next page event fired`);
        const { hasChildren } = event.detail;
        await nylasConnector.threads.nextThreadPage(hasChildren ? 'append' : 'overwrite', !hasChildren);
      },
      previous: async (event: CustomEvent<PaginationEvent>, nylasConnector: NylasMailboxConnector) => {
        debug(`[nylas-pagination] Previous page event fired`);
        const { hasChildren } = event.detail;
        await nylasConnector.threads.previousThreadPage(hasChildren ? 'append' : 'overwrite', !hasChildren);
      },
    },
    fireRegisterEvent: true,
  })
  render() {
    const hasChildren = this.host.children.length > 0;
    if (hasChildren) {
      return (
        <Host>
          <div class="container" ref={r => (this.containerElement = r)}>
            <slot></slot>
            {this.canScroll && (
              <div class={'pagination'}>
                <sp-theme scale="medium" color="dark">
                  {this.state === 'ready' && this.hasNextPage && (
                    <sp-button onClick={this.nextPage}>
                      <div slot="icon">
                        <refresh-icon class={'refres-icon'} />
                      </div>{' '}
                      Load more...
                    </sp-button>
                  )}
                </sp-theme>
              </div>
            )}
          </div>
        </Host>
      );
    }

    return (
      <Host>
        <sp-theme scale="medium" color="dark">
          {this.hasPreviousPage && (
            <sp-button class={'previous'} parr="previous" onClick={this.previousPage}>
              <div slot="icon">
                <chevron-icon class={'previous-icon'} />
              </div>{' '}
              Previous
            </sp-button>
          )}
          {this.hasPreviousPage && this.hasNextPage && ' '}
          {this.hasNextPage && (
            <sp-button class={'next'} parr="next" onClick={this.nextPage}>
              <div slot="icon">
                <chevron-icon class={'next-icon'} />
              </div>{' '}
              Next
            </sp-button>
          )}
        </sp-theme>
      </Host>
    );
  }
}
